import React from 'react';
import classNames from 'classnames';

import { Text } from '../Typography';
import type { HeadingProps } from '../Typography/types';

import styles from './Step.module.scss';

export interface StepProps {
    index: number;
    title: string;
    description?: string;
    children?: React.ReactNode;
    inverted?: boolean;
    titleAs?: HeadingProps['as'];
}

const Step = ({
    index,
    title,
    description,
    children,
    inverted,
    titleAs = 'h4',
}: StepProps) => {
    const classList = classNames(styles.step, {
        [styles['step--inverted']]: inverted,
    });

    return (
        <li className={classList}>
            <div className={styles['step__index-container']}>
                <div className={styles.step__index}>
                    <Text.Body size="large-600" variant="line">
                        {index + 1}
                    </Text.Body>
                </div>
            </div>
            <div className={styles['step__content-container']}>
                <Text.Heading
                    variant="h4"
                    as={titleAs}
                    className={styles.step__title}
                >
                    {title}
                </Text.Heading>
                {description && (
                    <Text.Body
                        size="default-400"
                        variant="p"
                        className={styles.step__description}
                    >
                        {description}
                    </Text.Body>
                )}
                {children}
            </div>
        </li>
    );
};

export default Step;
